	module.exports = {"data":{"file":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":0.9657118055555556,"src":"/static/avatar-820706df6fdb07a9b021a790449fec17-381d6.jpg","srcSet":"/static/avatar-820706df6fdb07a9b021a790449fec17-62f05.jpg 200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-dc2b4.jpg 400w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-381d6.jpg 800w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-e5d70.jpg 1200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-d14e0.jpg 1600w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-9cc71.jpg 2225w","srcWebp":"/static/avatar-820706df6fdb07a9b021a790449fec17-fefde.webp","srcSetWebp":"/static/avatar-820706df6fdb07a9b021a790449fec17-5eed2.webp 200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-68e3c.webp 400w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-fefde.webp 800w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-300fe.webp 1200w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-ed5bd.webp 1600w,\n/static/avatar-820706df6fdb07a9b021a790449fec17-9442a.webp 2225w","sizes":"(max-width: 800px) 100vw, 800px"}}},"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"Senior full stack engineer <a href=\"https://geekbot.io\">@geekbot_io</a>, co-organizer of <a href=\"https://devitconf.org\">@devitconf</a> & <a href=\"https://www.meetup.com/Thessaloniki-Node-js-Meetup/\">@skgnodejs</a>, host of <a href=\"http://devastation.tv\">Devastation Podcast</a>.","siteUrl":"https://kostasbariotis.com"}},"mainPost":{"html":"<p>Back to base!</p>\n<p>Back from two weeks of <a title=\"Thasos\" href=\"http://el.wikipedia.org/wiki/%CE%98%CE%AC%CF%83%CE%BF%CF%82\">Greek </a><a title=\"Samothraki\" href=\"http://el.wikipedia.org/wiki/%CE%A3%CE%B1%CE%BC%CE%BF%CE%B8%CF%81%CE%AC%CE%BA%CE%B7\">Vacations</a>. As usually, when i'm returning to base after being away for such time i must get on track.I will share some of the most important articles and news i have missed over the past two weeks.</p>\n<p>Backbone.js. Time to jump from a beginner to the next level! <a href=\"http://www.smashingmagazine.com/2013/08/09/backbone-js-tips-patterns/\" target=\"_blank\" rel=\"noreferrer noopener\">http://www.smashingmagazine.com/2013/08/09/backbone-js-tips-patterns/</a></p>\n<p>Mozilla has started a series of videos introducing some key concepts of the upcoming and still in development mode Firefox OS. Check them out. Part <a href=\"https://hacks.mozilla.org/2013/06/firefox-os-for-developers-the-platform-html5-deserves/\">One</a>, <a href=\"https://hacks.mozilla.org/2013/07/app-discovery-firefox-os-for-developers-the-platform-html5-deserves/\">Two</a>, <a href=\"https://hacks.mozilla.org/?p=21869\">Three</a>, <a href=\"https://hacks.mozilla.org/2013/08/firefox-marketplace-and-alternatives-firefox-os-for-developers-the-platform-html5-deserves/\">Four</a></p>\n<p>In case of wondering how EcmaScript 6 is doing <a title=\"Addy Osmani\" href=\"http://addyosmani.com/\">Addy Osmani</a> has some updated notes on it. <a href=\"http://addyosmani.com/blog/tracking-es6-support/\" target=\"_blank\" rel=\"noreferrer noopener\">http://addyosmani.com/blog/tracking-es6-support/</a></p>\n<p>DailyJS explored Facebook's React library. <a href=\"http://feedproxy.google.com/~r/dailyjs/~3/WREdssR9BPc/react\" target=\"_blank\" rel=\"noreferrer noopener\">http://feedproxy.google.com/~r/dailyjs/~3/WREdssR9BPc/react</a></p>\n<p>NetTuts+ have a nice tutorial on Express along with MVC patterns. Using it conjuction with Node.js and you should be fine. <a href=\"http://net.tutsplus.com/tutorials/javascript-ajax/build-a-complete-mvc-web-site-with-expressjs/?utm_source=feedburner&amp;utm_medium=feed&amp;utm_campaign=Feed%3A+nettuts+%28Nettuts%2B%29\">http://net.tutsplus.com/tutorials/javascript-ajax/build-a-complete-mvc-web-site-with-expressjs/?utm<em>source=feedburner&#x26;utm</em>medium=feed&#x26;utm_campaign=Feed%3A+nettuts+%28Nettuts%2B%29</a></p>\n<p>NoFlo team is not only visioning a new way of analyzing code but also a new method of writing it. Using flow-based charts and methods. <a href=\"http://www.kickstarter.com/projects/noflo/noflo-development-environment\">http://www.kickstarter.com/projects/noflo/noflo-development-environment</a></p>","excerpt":"Back to base! Back from two weeks of  Greek  Vacations . As usually, when i'm returning to base after being away for such time i must get on…","frontmatter":{"date":"August 19, 2013","path":"/what-ive-missed-over-vacations/","tags":null,"title":"What i've missed over vacations","draft":null}},"nextPost":{"html":"<p>Ο <a title=\"Brad Frost Web\" href=\"bradfrostweb.com\">Brad Frost</a>, απ' τους αγαπημένους μου σχεδιαστές, ταλαντούχος ομιλητής μας εισάγει σ' έναν κόσμο όπου ο καθένας μας είναι ελεύθερος να παράγει και να μοιράσει στους υπόλοιπους τα σκουπίδια του!</p>\n<p>[vimeo http://www.vimeo.com/63437853 w=500&#x26;h=281]</p>\n<p>Οι σημειώσεις που κράτησα από το βίντεο:</p>\n<ul>\n\t<li>10% του συνόλου των βιβλίων καθώς και 10% του συνόλου των βίντεο/φωτογραφιών του διαδικτύου παράχθηκαν μόλις <span style=\"text-decoration:underline;\">πέρσι!</span></li>\n\t<li>Μόλις το 35% του παγκόσμιου πληθυσμού έχει πρόσβαση στον παγκόσμιο ιστό. Οι υπόλοιποι κατευθύνονται κατά πάνω του με υπερφυσική ταχύτητα!</li>\n\t<li>Όταν η πληροφορία είναι φθηνή, η προσοχή της είναι ακριβή. Με άλλα λόγια η ποιότητα της πληροφορίας είναι αντιστρόφως ανάλογη της αποδοχής που θα λάβει! - <a title=\"James Gleick\" href=\"http://en.wikipedia.org/wiki/James_Gleick\">Jack Gleick</a></li>\n\t<li>90% του συνόλου είναι ΜΑΛΑΚΙΕΣ! -<a title=\"Sturgeon's Law\" href=\"http://en.wikipedia.org/wiki/Sturgeon's_Law\">Sturgeon's Law</a></li>\n</ul>","excerpt":"Ο  Brad Frost , απ' τους αγαπημένους μου σχεδιαστές, ταλαντούχος ομιλητής μας εισάγει σ' έναν κόσμο όπου ο καθένας μας είναι ελεύθερος να…","frontmatter":{"date":"April 08, 2013","path":"/peges-empneuses-102/","tags":null,"title":"Πηγες Εμπνευσης 102: Περι σκουπιδιων ο λογος","draft":null}}},"pathContext":{"mainPostPath":"/what-ive-missed-over-vacations/","nextPostPath":"/peges-empneuses-102/"}}

